<template>
  <router-view></router-view>
</template>

<script>
import { getWeather, setUpdater, clearUpdater } from './components/api';

export default {
  mounted()
  {
    this.$store.getters.getCities.forEach(item =>
    {
      setUpdater(item.city, async () =>
        {
            const weather = await getWeather(item.city);
            if (!weather) {
                clearUpdater(item.city);
                this.$store.commit('updateCity', {city : item.city, unvalid : true});
                return;
            }
            this.$store.commit('updateCity', {city : item.city, deg : weather.main.temp, ...weather.weather[0], wind : weather.wind.speed});
        })
    })
  }
}


</script>