import { createStore } from 'vuex';
import { getStorage, setStorage } from '../components/data';
import { deleteUpdater } from '../components/api';

export default createStore({
  state: {
    allCities : getStorage('allCities')
  },
  mutations: {
    updateCity(state, item)
    {
      state.allCities.filter(x => x.city === item.city).map(x => Object.assign(x, item));
    },
    addCity(state, city)
    {
      state.allCities.push({city});
      setStorage('allCities', state.allCities);
    },
    deleteCity(state, city)
    {
      deleteUpdater(city);
      state.allCities = state.allCities.filter(item => item.city !== city);
      setStorage('allCities', state.allCities);
    }
  },
  actions: {
  },
  getters: {
    getCities(state)
    {
      return state.allCities;
    }
  }
})
