<template>
  <div class="all-cities-weather">
    <transition-group name="items">
      <div
        class="city-weather"
        v-for="item in cities"
        :key="item.city"
        :class="{ 'unvalid-weather': item.unvalid }"
        v-color:[item.icon]
        @click="
          $router.push({ name: 'searchCity', params: { city: item.city } })
        "
      >
        <h1>{{ item.city.length > 15? `${item.city.substring(0, 15)}...` : item.city }}</h1>
        <h1>{{ item.deg ? `${item.deg}C°` : "-" }}</h1>
      </div>
    </transition-group>
  </div>
</template>

<script>
export default {
  props: {
    cities: {
      type: Array,
      required: true,
    },
  },
};
</script>
