<template>
    <div class="search-container">
        <open-panel :city="getCity"></open-panel>
        <transition name="find-city">
            <find-city :city="getCity" v-show="getCity"></find-city>
        </transition>
    </div>
</template>

<script>
import OpenPanel from './OpenPanel.vue';
import FindCity from './FindCity.vue';
import data from './search';

export default {
    setup() {
        return {...data()}
    },
    components : {
        'open-panel' : OpenPanel,
        'find-city' : FindCity
    },
    watch : {
        'getParams'()
        {
            this.city = this.getParams;
        }
    }
}


</script>