import { ref, computed } from 'vue';
import { useRoute } from 'vue-router';
import { useStore } from 'vuex';

export default () =>
{
    const route = ref(useRoute());
    const store = ref(useStore());

    const getParams = computed(() => route.value.params.city);

    const city = ref(getParams.value);
    
    const getCity = computed(() => store.value.getters.getCities.find(item => item.city ===city.value));

    return {
        getCity,
        getParams,
        city
    }
}