<template>
    <div class="container">
        <nav>
            <button @click="$router.push('/search')"><i class="fas fa-search"></i></button>
            <input type="text" placeholder="поиск" class="search-home" v-model="searchCity">
        </nav>
        <all-cities :cities="getCities"></all-cities>
        <button class="add" @click="openPopup">+</button>
        <transition name="shadow">
            <div class="shadow" v-show="open"></div>
        </transition>
        <add-cities ref="popup" :getCities="getCitiesName"></add-cities>
    </div>
</template>

<script>
import AllCities from './AllCities.vue';
import AddCities from './AddCities.vue';
import data from './home';

export default {
    setup()
    {
        return {...data()}
    },
    components : {
        'all-cities' : AllCities,
        'add-cities' : AddCities
    },
    methods : {
        async openPopup()
        {
            this.open = true;
            await this.$refs.popup.openPopup()
            .then(city => 
            {
                this.$store.commit('addCity', city);
                this.setCity(city);
            })
            .catch(() => {return;});
            this.open = false;
        }
    }
}

</script>
