<template>
  <transition name="add-cities">
    <div class="add-cities-container" v-show="open">
      <div class="add-cities">
        <input type="text" placeholder="Введите город" v-model="city" @keydown.enter="addCity" :class="{'red-border' : error}"/>
        <div class="add-cities-footer">
            <button class="green-button red-button" @click="close">Отмена</button>
            <button class="green-button" @click="addCity" :disabled="!city.length"
            :class="{'disabled-button' : !city.length}">Добавить город</button>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>


export default {
  props : {
    getCities : {
      type : Array,
      required : true
    }
  },
  resolve : null,
  reject : null,
  data()
  {
    return {
      open : false,
      city : '',
      error : false
    }
  },
  methods : {
    openPopup()
    {
      this.open = true;
      return new Promise((resolve, reject) =>
      {
        this.$options.resolve = resolve;
        this.$options.reject = reject;
      })
    },
    close()
    {
      this.city = '';
      this.$options.reject();
      this.open = false;
    },
    addCity()
    {
      if (this.getCities.map(city => city.toUpperCase()).indexOf(this.city.toUpperCase()) !== -1) {this.error = true; return;}
      if (!this.city.length) {this.error = true; return;}

      this.$options.resolve(this.city);
      this.open = false;
      this.city = '';
    }
  },
  watch : {
    city()
    {
      this.error = false;
    }
  }
};


</script>
