import { ref, computed } from 'vue';
import { useStore } from 'vuex';

export default () =>
{
    const store = ref(useStore());
    const searchCity = ref('');

    const getCitiesName = computed(() => store.value.getters.getCities.map(item => item.city));
    const validCities = computed(() => getCitiesName.value.filter(city => city.toUpperCase().includes(searchCity.value.toUpperCase())));

    return {
        searchCity,
        validCities
    }
}