import { ref, computed } from 'vue';
import { useStore } from 'vuex';
import { setUpdater, getWeather, clearUpdater } from './api';

export default () =>
{
    const store = ref(useStore());

    const open = ref(false);
    const searchCity = ref('');

    const getCities = computed(() => store.value.getters.getCities.filter(item => item.city.toUpperCase().includes(searchCity.value.toUpperCase())));
    const getCitiesName = computed(() => store.value.getters.getCities.map(item => item.city));

    const setCity = city =>
    {
        setUpdater(city, async () =>
        {
            const weather = await getWeather(city);
            if (!weather) {
                clearUpdater(city);
                store.value.commit('updateCity', {city, unvalid : true});
                return;
            }
            store.value.commit('updateCity', {city, deg : weather.main.temp, ...weather.weather[0], wind : weather.wind.speed});
        })
    }

    return {
        getCities,
        getCitiesName,
        open,
        setCity,
        searchCity
    }
}