import axios from 'axios';

const intervals = new Map();

async function getWeather(city)
{
    const weather = await axios.get(`https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=721d8b44e2c10dbf7a2db2accac869a2&units=metric&lang=ru`)
    .then(x => x.data)
    .catch(() => null);
    return weather;
}

const setUpdater = (city, cb) =>
{
    intervals.set(city, setInterval(cb, 5000));
    cb();
}

const clearUpdater = city =>
{
   clearInterval(intervals.get(city));
}

const deleteUpdater = city =>
{
    intervals.delete(city);
}

export { setUpdater, getWeather, clearUpdater, deleteUpdater }