<template>
    <div class="find-city-container" v-color:[city?.icon] :class="{'unvalid-weather' : city?.unvalid}">
        <header>
            <div class="city-description">
                <h1 v-show="!city?.unvalid">{{ city?.city }}</h1>
                <h1 v-show="!city?.unvalid">{{ city?.deg ? `${city?.deg}C°` : '-'}}</h1>
                <h1 v-show="city?.unvalid">Такого города не существует</h1>
            </div>
            <img :src="`http://openweathermap.org/img/wn/${city?.icon}@2x.png`" v-if="city && !city?.unvalid">
