import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

const app = createApp(App);

app.use(store);

app.use(router);

app.directive('color', (el, params) =>
{
    const icon = params.arg;
    if (!icon) {return;}
    if (icon[icon.length-1] === 'n') {el.style.background = 'rgb(5, 4, 77)'; return;}
    
    switch (icon) {
        case '03d': el.style.background = 'rgb(116, 114, 233)'; break;
        case '01d':
        case '02d': el.style.background = 'rgb(250, 171, 2)'; break;
        case '13d': el.style.background = 'blue'; break;
        case '50d': el.style.background = 'rgb(183, 185, 185)'; break;
        default : el.style.background = 'rgb(88, 88, 88)';
    }
})

app.mount('#app');
