<template>
    <div class="search-panel">
        <nav>
            <button style="transform : scale(0.9); padding : 5px 10px;" @click="$router.push('/')"><i class="fas fa-home"></i></button>
            <input type="text" placeholder="поиск" v-model="searchCity">
        </nav>
        <div class="panel-cities">
            <button :class="{'active-city' : city?.city === item}" v-for="item in validCities" :key="item"
            @click="$router.push(`/search/${item}`)">{{ item.length > 10? `${item.substring(0, 10)}...` :  item}} <i class="fas fa-trash" @click.stop="$store.commit('deleteCity', item)"></i></button>
        </div>
    </div>
</template>

<script>
import data from './panel';

export default {
    setup() {
        return {...data()}
    },
    props : {
        city : {}
    }
}


</script>